<template>
    <div class='entry' data-v-facloseinput>
        <form data-v-facloseinput>
            <div class='entryInputBlock' data-v-facloseinput>
                <p data-v-faentry>指定月</p>
                <input type="text" name="periodInput" v-model="period">
            </div>
            <div class='entryInputBlock' data-v-facloseinput>
                <p data-v-faentry>状態</p>
                <input type="text" name="status" v-model="status" readonly>
            </div>
        </form>
        <div class='entrySummary' data-v-facloseinput>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "executeClose" v-show="isOpen">締め処理実行</div>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "reOpen" v-show="enableRepoen">再オープン</div>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "refreshData">最新情報に更新</div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'FAcloseInput',

    props:['bid'],

    data(){
        return{
            period : null,
            status : null,
            isOpen : false,
            enableRepoen : false
        }
    },

    // ------------------------------------
    //  初期化処理
    // ------------------------------------
    created()
    {
        // Todo Bank情報取得API呼出
        // APIのコールバック内で下記処理を実行
        // 現在月の設定
        this.period = '2018/09'
        // ステータス
        this.status = 'Open'
        // 締め処理ボタン表示可否
        this.isOpen = true
        // 再オープンボタン表示可否
        this.enableRepoen = false
    },
    
    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:
    {
        // ------------------------------------
        //  締め処理実行
        // ------------------------------------
        executeClose(){
        },

        // ------------------------------------
        //  再オープン
        // ------------------------------------
        reOpen(){
        },

        // ------------------------------------
        //  最新情報に更新
        // ------------------------------------
        refreshData(){
        },
    }

}
</script>


<style scoped>
    .entry[data-v-facloseinput]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }
    .entryInputBlock[data-v-facloseinput]{
        float:left;
        padding:5px;
    }
    .entrySummary[data-v-facloseinput]{
        clear:left;
        padding:5px;
    }

    .compButton[data-v-facloseinput]{
        margin : 15px 0px;
    }

</style>


