<template>
    <section data-v-faclose>
        <!-- 画面ヘッダ -->
        <ViewHeader 
            v-bind:moduleName   =   'moduleName'
            v-bind:viewName     =   'viewName'
        /> 

        <!-- 家計簿入力ヘッダ -->
        <FAheader 
            v-bind:header = 'faHeader'
            v-on:header-bank-change-event = 'changeBank'
        />

        <!-- 締め処理入力用コンポーネント -->
        <FAcloseInput 
            v-bind:bid = 'bid'
        />

    </section>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

import ViewHeader   from '../components/ViewHeader'
import FAheader     from '../components/FAheader'
import FAcloseInput from '../components/FAcloseInput'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'


export default {
    name : 'FAclose',
    mixins : [apiUrls],
    
    // ------------------------------------
    //  使用コンポーネント一覧
    // ------------------------------------
    components : {
        ViewHeader,
        FAheader,
        FAcloseInput,
        Modal,
        Loading
    },

    // ------------------------------------
    //  初期化処理
    // ------------------------------------
    created : function(){

        // FAヘッダに現在日付を設定
        let dt = new Date()
        this.faHeader.year  = dt.getFullYear()
        this.faHeader.month = dt.getMonth()+1
        this.faHeader.day   = dt.getDate()
    },

    // ------------------------------------
    //  データ
    // ------------------------------------
    data(){
        return {
            moduleName : 'FA',
            viewName : '月次締め処理',

            // ------------------------------------
            // FAヘッダ通信用オブジェクト
            // ------------------------------------
            faHeader: {
                year : null,
                month : null,
                day : null,
                bank : '',
                balance : 10
            },

            // BankId
            bid : 0
        }
    },

    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:{
        changeBank(bid){
            this.bid = bid
        }
    }

}
</script>


